import React from "react";
import { useParams } from "react-router-dom";

import { CCard, CCardHeader, CCardBody } from "@coreui/react";
import { useSelector } from "react-redux";

function findSubjectById(array, id) {
  var subject;
  for (let index = 0; index < array.length; index++) {
    for (let i = 0; i < array[index]["days"].length; i++) {
      array[index]["days"][i].subjects.find(function (element) {
        if (element.id === Number(id)) {
          subject = element;
          return element;
        }
      });
    }
  }
  return subject;
}

const Subject = () => {
  /**
   * Todo: Fetch subject by id
   * @info when this component mounting fetch specific subject by its id
   */
  const weeksData = useSelector((state) => state.data.weeks);
  console.log(weeksData);
  let { id } = useParams();
  let subject = findSubjectById(weeksData, id);
  console.log(subject);

  let Objectives = subject["learningObjectives"].split(",");

  return (
    <>
      {/* <div classNamse="h1">Subject {id}</div> */}
      <CCard>
        <CCardHeader style={{ margin: "0 auto" }} className="h1">
          {subject.title}
        </CCardHeader>
        <CCardBody>
          {/* Learning Objectives: Part */}
          <div>
            <div className="h2">Learning Objectives</div>
            <ul>
              {Objectives.map((txt) => (
                <li>{txt}</li>
              ))}
            </ul>
          </div>
          <div>
            {/* Lectures Part */}
            <div className="h2">Lectures</div>
          </div>
          <div>
            <ul>
              {subject["lectures"].map((txt) => (
                <a href={txt.url} style={{ fontSize: "15px" }} target="_blank">
                  {" "}
                  {txt.title}
                  <br />
                </a>
              ))}
            </ul>
          </div>
          {/* Resources Part */}
          <div>
            <div className="h2">Resources</div>
          </div>
          <div>
            <ul>
              {subject["resourses"].map((txt) => (
                <a href={txt.url} style={{ fontSize: "14px" }} target="_blank">
                  {" "}
                  {txt.title}
                  <br />
                </a>
              ))}
            </ul>
          </div>

          {/* Exercises Part */}
          <h2>Exercises</h2>
          {/* Basic Exercises Part */}
          <div className="h3">Basic</div>
          {subject["exercises"]
            .sort((a, b) => a.order - b.order)
            .map(
              (txt) =>
                txt.level === "Basic" && (
                  <div class="card w-75" style={{ borderRadius: "0" }}>
                    <div
                      class="card w-80"
                      style={{
                        fontWeight: "bold",
                        fontSize: "15px",
                        borderRadius: "0",
                        paddingLeft: "0.8em",
                      }}
                    >
                      {" "}
                      Q{txt.order}:
                    </div>

                    <p
                      style={{
                        fontWeight: "549",
                        fontSize: "16px",
                        marginLeft: "2px",
                        fontFamily: "Times New Roman",
                        paddingLeft: "0.8em",
                      }}
                    >
                      {txt.question}
                    </p>

                    <div
                      class="card w-80 "
                      style={{
                        fontWeight: "bold",
                        backgroundColor: "#3C4B64",
                        height: "200px",
                        marginLeft: "2px",
                        borderRadius: "0",
                        fontFamily: "Courier New",
                        paddingLeft: "0.8em",
                      }}
                    >
                      <p style={{ fontWeight: "bold", color: "#00FFFF" }}>
                        {txt.signature}
                      </p>
                    </div>
                    {/* Hint Part */}

                    {txt.hint && (
                      <p
                        style={{
                          fontSize: "15px",
                          color: "black",
                          paddingLeft: "0.8em",
                        }}
                      >
                        <span style={{ fontWeight: "bold", color: "#FF0000" }}>
                          Hint:
                        </span>
                        <span style={{ paddingLeft: "0.3em" }}>{txt.hint}</span>
                      </p>
                    )}
                  </div>
                )
            )}

          {/* Intermediate Exercises Part */}
          <div className="h3">Intermediate</div>
          {subject["exercises"]
            .sort((a, b) => a.order - b.order)
            .map(
              (txt) =>
                txt.level === "Intermediate" && (
                  <div class="card w-75" style={{ borderRadius: "0" }}>
                    <div
                      class="card w-80"
                      style={{
                        fontWeight: "bold",
                        fontSize: "15px",
                        borderRadius: "0",
                        paddingLeft: "0.8em",
                      }}
                    >
                      {" "}
                      Q{txt.order}:
                    </div>

                    <p
                      style={{
                        fontWeight: "549",
                        fontSize: "16px",
                        marginLeft: "2px",
                        fontFamily: "Times New Roman",
                        paddingLeft: "0.8em",
                      }}
                    >
                      {txt.question}
                    </p>

                    <div
                      class="card w-80 "
                      style={{
                        fontWeight: "bold",
                        backgroundColor: "#3C4B64",
                        height: "200px",
                        marginLeft: "2px",
                        borderRadius: "0",
                        fontFamily: "Courier New",
                        paddingLeft: "0.8em",
                      }}
                    >
                      <p style={{ fontWeight: "bold", color: "#00FFFF" }}>
                        {txt.signature}
                      </p>
                    </div>
                    {/* Hint Part */}

                    {txt.hint && (
                      <p
                        style={{
                          fontSize: "15px",
                          color: "black",
                          paddingLeft: "0.8em",
                        }}
                      >
                        <span style={{ fontWeight: "bold", color: "#FF0000" }}>
                          Hint:
                        </span>
                        <span style={{ paddingLeft: "0.3em" }}>{txt.hint}</span>
                      </p>
                    )}
                  </div>
                )
            )}
          {/* Advanced Exercises Part */}
          <div className="h3">Advanced</div>
          {subject["exercises"]
            .sort((a, b) => a.order - b.order)
            .map(
              (txt) =>
                txt.level === "Advanced" && (
                  <div class="card w-75" style={{ borderRadius: "0" }}>
                    <div
                      class="card w-80"
                      style={{
                        fontWeight: "bold",
                        fontSize: "15px",
                        borderRadius: "0",
                        paddingLeft: "0.8em",
                      }}
                    >
                      {" "}
                      Q{txt.order}:
                    </div>

                    <p
                      style={{
                        fontWeight: "549",
                        fontSize: "16px",
                        marginLeft: "2px",
                        fontFamily: "Times New Roman",
                        paddingLeft: "0.8em",
                      }}
                    >
                      {txt.question}
                    </p>

                    <div
                      class="card w-80 "
                      style={{
                        fontWeight: "bold",
                        backgroundColor: "#3C4B64",
                        height: "200px",
                        marginLeft: "2px",
                        borderRadius: "0",
                        fontFamily: "Courier New",
                        paddingLeft: "0.8em",
                      }}
                    >
                      <p style={{ fontWeight: "bold", color: "#00FFFF" }}>
                        {txt.signature}
                      </p>
                    </div>
                    {/* Hint Part */}

                    {txt.hint && (
                      <p
                        style={{
                          fontSize: "15px",
                          color: "black",
                          paddingLeft: "0.8em",
                        }}
                      >
                        <span style={{ fontWeight: "bold", color: "#FF0000" }}>
                          Hint:
                        </span>
                        <span style={{ paddingLeft: "0.3em" }}>{txt.hint}</span>
                      </p>
                    )}
                  </div>
                )
            )}
        </CCardBody>
      </CCard>
    </>
  );
};
export default Subject;
